import React, { Component } from 'react';

import NavbarPage from "../../components/Navbar/Navbar_Page";
import Section from './section';
import Service from "../../components/Service/service";
import Team from '../../components/Team/Team';
import Contact from "../../components/Contact/Contact";
import Social from "../../components/Social/Social";
import Footer from "../../components/Footer/footer";

class Index6 extends Component {
    constructor(props) {
        super(props);
        this.state = {
            navItems : [
                { id: 1, idnm : "home", navheading: "Home" },
                { id: 2 , idnm : "services", navheading: "Services" },
             // { id: 2, idnm : "features", navheading: "Features" },
             // { id: 3, idnm : "pricing", navheading: "Pricing" },
                { id: 4, idnm : "team", navheading: "Team" },
             // { id: 5, idnm : "blog", navheading: "Blog" },
                { id: 6, idnm : "contact", navheading: "Contact" }

            ],
            navClass : ""
        };
    }
    
    render() {
        return (
            <React.Fragment>

                {/* Importing Navbar */}
                <NavbarPage navItems={this.state.navItems} navClass={this.state.navClass} />

                {/* section */}
                <Section/>

                {/* services */}
                <Service  sectionClass=""/>

                {/* team */}
                <Team/>

                {/* contact */}
                <Contact/>

                {/* social */}
                <Social />

                {/* footer */}
                <Footer/>
            </React.Fragment>
        );
    }
}

export default Index6;