import React, { Component } from "react";
import { Container, Row, Col, Form, Button } from "reactstrap";

class Section extends Component {
  render() {
    return (
      <React.Fragment>
        <section className="section bg-home home-half" id="home">
          <div className="bg-overlay"></div>
          <div className="display-table">
            <div className="home-cell-bottom">
              <Container>
                <Row>
                  <Col
                    lg={{ size: 8, offset: 2 }}
                    className="text-white text-center"
                  >
                    <h1 className="home-title">
                    <img
                      src="assets/images/paral_logo.png"
                      alt="PARAL"
                    />
                    </h1>
                    <p className="pt-3 home-desc">
                    At PARAL, we know that demand for staff and lack of time, for quality and service around patients
                    is an increasing and negative factor in the Danish healthcare sector. In addition, we also know that
                    better health, well-being and increased quality of life come through continuous insight into one's
                    own health as well as external monitoring of patients, which is also called ‘Hospital At Home’.
                    </p>
                    <div className="text-center subscribe-form mt-5">
                      <Form>
                        <input type="text" placeholder="Enter email and reach out to us!" required />
                        <Button
                          color="none"
                          type="submit"
                          className="btn-primary"
                        >
                          Subscribe
                        </Button>
                      </Form>
                    </div>
                  </Col>
                </Row>
              </Container>
            </div>
          </div>
        </section>
      </React.Fragment>
    );
  }
}

export default Section;
