import React, { Component } from "react";
import { Container, Row } from "reactstrap";

//Import Section Title
import SectionTitle from "../common/section-title";
import ServiceBox from "./services-box";

class Process extends Component {
  constructor(props) {
    super(props);
    this.state = {
      services1: [
        {
          icon: "pe-7s-diamond",
          title: "Development",
          desc:
            "We are developing an user friendly ECG device that is specialized for long term usability and delivering an unique way to collect data before and after medical treatment.",
        },
        {
          icon: "pe-7s-display2",
          title: "Our technology",
          desc:
            "PARAL innovates the traditional ECG device technology with a new touch of hardware, sensors and software which is combined in an unique design that is specialized for its intended use.",
        },
        {
          icon: "pe-7s-piggy",
          title: "Data monitoring",
          desc:
            "Our physical solution would not only provide health care professionals with pre-generated data, they will also get a live overview over their patients while they are monitored at home.",
        },
      ],
      services2: [
        {
          icon: "pe-7s-science",
          title: "Sustainability",
          desc:
            "The healthcare sector is full of unsustainable solutions which only cause problems for patients, health care professionals and the environment. PARAL eliminates the logistical and non-reusable products with one solution that motivates nudging and gives patients the possibility to provide efficient results with our device.",
        },
      ],
    };
  }

  render() {
    return (
      <React.Fragment>
        <section className={"section " + this.props.sectionClass} id="services">
          <Container>
            {/* section title */}
            <SectionTitle
              title="Our Services"
              desc="We craft digital, graphic and dimensional thinking, to create category leading brand experiences that have meaning and add a value for our clients."
            />

            <Row className="mt-4">
              {/* services box */}
              <ServiceBox services={this.state.services1} />
            </Row>

            <Row className="mt-4">
              {/* service box */}
              <ServiceBox services={this.state.services2} />
            </Row>
          </Container>
        </section>
      </React.Fragment>
    );
  }
}

export default Process;
