import React, { Component } from "react";
import { Container, Row } from "reactstrap";

//Import Section Title
import SectionTitle from "../common/section-title";
import TeamBox from "./TeamBox";

class Clients extends Component {
  constructor(props) {
    super(props);
    this.state = {
      teams: [
        {
          image: "assets/images/team/img-1.jpg",
          title: "Martin E. Johansen",
          occupation: "Process & Innovation",
          desc: "Founder & CEO",
        },
        {
          image: "assets/images/team/img-2.jpg",
          title: "Yousef El-Sharifi",
          occupation: "Medical student",
          desc: "Clinical & Regulatory advisor",
        },
        {
          image: "assets/images/team/daniel.png",
          title: "Daniel Larsen",
          occupation: "MSc. Eng. Computer Science",
          desc: "Software Engineer",
        },
        {
          image: "assets/images/team/img-4.jpg",
          title: "Kasper Bjørnskov",
          occupation: "B.Eng Electrical Engineering",
          desc: "Digital sensor processing & PCB architecture",
        },
        {
          image: "assets/images/team/img-4.jpg",
          title: "Samuel Anker",
          occupation: "B.Eng Electrical Engineering",
          desc: "Analog sensor processing & PCB architecture",
        },
      ],
    };
  }

  render() {
    return (
      <React.Fragment>
        <section className="section pt-4" id="team">
          <Container>
            {/* section title */}
            <SectionTitle
              title="BEHIND THE PEOPLE"
              desc="It is a long established fact that create category leading brand experiences a reader will be distracted by the readable content of a page when looking at its layout."
            />

            <Row className="mt-5">
              {/* team box */}
              <TeamBox teams={this.state.teams} />
            </Row>
          </Container>
        </section>
      </React.Fragment>
    );
  }
}

export default Clients;
